*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

html {
    font-size: 62.5%;
}


/*general styles*/
h1 {
    font-family: 'OpenSans-Bold', sans-serif;
    font-weight: 700;
    font-size: 3.2rem;
    text-decoration: none;
    color: var(--text-blue);
    margin-bottom: 2rem;
}


button, .button {
    background: #ffb900;
    color: #363B42;
    padding: 1rem 2rem;
    border: none;
    cursor: pointer;
    font-weight: bold;
}

button:hover {
    background: #FBA900;
}

/*form*/
.add__title {
    text-align: center;
    margin: 6rem 0 3rem 0;
}
.create-form {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin: 3rem;
}

.create-form__input {
    padding: 0.8rem 1.6rem;
    margin: 1rem 0 3rem 0;
    border-radius: 0.7rem;
    background-color: rgb(250, 250, 250);
    border: 0.1rem solid rgb(238, 238, 238);
    -webkit-transition: all 0.4s ease 0s;
    -o-transition: all 0.4s ease 0s;
    transition: all 0.4s ease 0s;
    color: rgb(118, 118, 118);
    height: 4rem;
}

.create-form__input--height {
    height: 20rem;
}

.create-form label {
    font-family: 'OpenSans-Regular', sans-serif;
    font-size: 1.6rem;
    font-weight: 500;
}

.create-form__u-flex {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}

.create-form__btn {
    width: 100%;
    max-width: 23rem;
}

.create-form__btn--margin {
    margin-bottom: 2rem;
}



/*responsive classes */
/*TabletView*/
@media only screen and (min-width: 45rem) {
	.add__title {
		margin: 3rem;
		text-align: left;
	}
    .create-form__u-flex {
        display: block;
    }
    .create-form__btn--margin {
        margin-right: 2rem;
    }

}


